import React, { useState, useEffect, useRef } from 'react';
import Chart from 'chart.js/auto';
import "./Styles.css";

const TiempoReal = () => {
  const link = "https://nestor1606-servidor1-22-ap4vv8gzb843.deno.dev";
  const [fechaHora, setFechaHora] = useState(new Date());
  const chartRef = useRef(null);
  const chartInstance = useRef(null); // Mantn una referencia a la instancia del grfico

  async function fetchApi() {
    try {
      const resultado = await fetch(`${link}/mostrar_datos_envivo`, {
        method: "GET",
        headers: { "Content-Type": "application/json" }
      });
      const resultadoJSON = await resultado.json();
      const ppm = resultadoJSON.info.data.ppm;

      if (chartInstance.current) { // Usar la referencia a la instancia del grfico
        const now = new Date();
        const label = now.toLocaleTimeString('es-ES');

        chartInstance.current.data.labels.push(label);
        chartInstance.current.data.datasets[0].data.push(ppm);

        if (chartInstance.current.data.labels.length > 30) {
          chartInstance.current.data.labels = chartInstance.current.data.labels.slice(10);
          chartInstance.current.data.datasets[0].data = chartInstance.current.data.datasets[0].data.slice(10);
        }

        chartInstance.current.update();
      }
    } catch (error) {
      console.error("Error fetching data:", error);
    }
  }

  useEffect(() => {
    const canvas = chartRef.current; // Obtener el elemento canvas a travs de la referencia
    if (canvas) {
      const ctx = canvas.getContext("2d");

      chartInstance.current = new Chart(ctx, { // Guardar la instancia del grfico en la referencia
        type: "line",
        data: {
          labels: [],
          datasets: [{
            label: 'PPM en tiempo real',
            data: [],
            fill: false,
            borderColor: 'rgb(75, 192, 192)',
            tension: 0.1
          }]
        },
        options: {
          responsive: true,
          scales: {
            x: {
              title: {
                display: true,
                text: 'Hora'
              }
            },
            y: {
              title: {
                display: true,
                text: 'PPM'
              },
              beginAtZero: true
            }
          }
        }
      });

      const intervalo = setInterval(() => {
        setFechaHora(new Date());
        fetchApi();
      }, 1000);

      return () => {
        clearInterval(intervalo);
        if (chartInstance.current) { // Usar la referencia a la instancia para la destruccin
          chartInstance.current.destroy();
          chartInstance.current = null;
        }
      };
    }
    // No dependencies here, se ejecuta solo al montar y desmontar
  }, []);

  const formatoFecha = fechaHora.toLocaleDateString('es-ES', {
    weekday: 'long',
    year: 'numeric',
    month: 'long',
    day: 'numeric'
  });

  const formatoHora = fechaHora.toLocaleTimeString('es-ES', {
    hour: '2-digit',
    minute: '2-digit',
    second: '2-digit'
  });

  return (
    <div className="content">
      <div id="sensors" className="tab-content active">
        <h2>Datos en Tiempo Real</h2>

        <div className="container-box one">
          <h3>Estado Real</h3>
          <p id="date-time">{(formatoFecha && formatoHora) ? `${formatoFecha}, ${formatoHora}` : "Cargando fecha y hora..."}</p>
        </div>

        <div className="container-box two">
          <h3>Estado del Sistema</h3>
          <div className="gauge-container">
            <canvas className="chart-container" id="gaugeChart" ref={chartRef}></canvas> {/* Asignar la referencia al canvas */}
          </div>
        </div>
      </div>
    </div>
  );
};

export default TiempoReal;