import React, { useEffect, useRef, useState } from "react";
import "./Styles.css";
import FiltroTiempo from "./FiltroTiempo";

const DatosHistoricos = () => {
  const chartRef = useRef(null);
  const chartInstance = useRef(null);
  const [filtro, setFiltro] = useState("semana");
  const [chartData, setChartData] = useState({ labels: [], data: [] });
  const [error, setError] = useState(null);

  const checkChart = () => {
    if (chartRef.current) {
      const ctx = chartRef.current.getContext("2d");
  
      if (chartInstance.current) {
        chartInstance.current.destroy(); // Destruir grfico previo si existe
      }

      chartInstance.current = new window.Chart(ctx, {
        type: "bar",
        data: {
          labels: chartData.labels,
          datasets: [
            {
              label: "Concentracin de Gas (PPM)",
              data: chartData.data,
              borderColor: "rgb(255, 82, 82)",
              backgroundColor: "rgba(255, 82, 82)",
            },
          ],
        },
        options: {
          responsive: true,
          maintainAspectRatio: false,
          plugins: {
            legend: {
              display: true,
              position: "top",
            },
          },
          scales: {
            x: {
              title: {
                display: true,
                text: "ID de Fuga",
              },
              ticks: {
                maxRotation: 0,
                minRotation: 0,
              },
              grid: {
                display: false,
              }
            },
            y: {
              title: {
                display: true,
                text: "PPM (Partes por Milln)",
              },
              beginAtZero: true,
              grid: {
                display: true,
              }
            }
          }
        },
      });
    }
  };

  async function callApi(params) {
    try {
      const response = await fetch("https://nestor1606-servidor1-22-ap4vv8gzb843.deno.dev/mostrar_datos", {
      // const response = await fetch("http://localhost:3001/mostrar_datos", {
        method: "GET",
        headers: { "Content-type": "application/json", "filtro": params }
      });

      if (!response.ok) {
        throw new Error(response.statusText);
      }
      const result = await response.json();
      let data = [],  labels = [];

      result.info.data.forEach(element => {
        labels.push(element["label"])
        data.push(element["ppm_total"])
      });
      console.log(result)
      setChartData({ labels: labels, data: data});
      setError(null); // Limpiar cualquier error anterior
    } catch (err) {
      setError(err.message);
      console.error("Failed to fetch data:", err);
    }
  }

  useEffect(() => {
    callApi(filtro);
  }, []);

  useEffect(() => {
    const timer = setTimeout(() => {
      checkChart();
    }, 100);

    return () => clearTimeout(timer);
  }, [chartData]);

  const changeFilter = async (event) => {
    setFiltro(event.target.value);
    await callApi(event.target.value);
    const timer = setTimeout(() => {
      checkChart();
    }, 100);

    return () => clearTimeout(timer);
  }

  return (
    <div className="content">
      <h2>Datos Histricos</h2>
      {error && <p style={{ color: 'red' }}>{error}</p>}
      <div className="historico-contenedor">
        <FiltroTiempo filtro={filtro} onFiltroChange={ changeFilter } />
        <div className="chart-container">
          <canvas ref={chartRef}></canvas>
        </div>
      </div>
    </div>
  );
};

export default DatosHistoricos;