import React from "react";
import "./Styles.css";

const FiltroTiempo = ({ filtro, onFiltroChange }) => {
  return (
    <div className="filtro-tiempo">
      <label htmlFor="filtro">Filtrar por:</label>
      <select id="filtro" value={filtro} onChange={(e) => onFiltroChange(e)}>
        <option value="ultimo_dia">Da</option>
        <option value="semana">Semana</option>
        <option value="tres_meses">Tres meses</option>
        <option value="seis_meses">Seis meses</option>
        <option value="ultimo_anio">Ao</option>
      </select>
    </div>
  );
};

export default FiltroTiempo;
